// /* 
//  * Tera design system variables
//  */

$tera-orange: #F98A00;
$tera-blue: #63A2E9;
$tera-green: #63C256;
$tera-yellow: #FFBE44;
$tera-red: #F86D77;
$tera-grey-1: #1BA0E2;
$tera-grey-2: #1BA0E2;
$tera-grey-3: #1BA0E2;
$tera-grey-4: #1BA0E2;

// /* 
//  * Tera functional variables
//  * 
//  */

// /* Components Color */
$color-primary: $tera-blue; 	// blue
$color-cta: $tera-orange;		// orange
$color-success: $tera-green; 	// green
$color-warning: $tera-yellow; 	// yellow
$color-error: $tera-red; 		// red
$color-disabled: #CCCCCC; 		// grey

// /* Background Color */
$color-background-primary: #F9FBFD; // soft greyish blue
$color-background-secondary: #F1F4F9;
$color-background-page: #F2F4F7;

// /* Text Color */
$color-text-primary: #404852;
$color-text-secondary: #7B8796;
$color-text-tertiary: #63758F; // used in grey background
$color-text-link: #5899D6;

// /* Border Color */
$color-border: #DCE1E8;

// /* Font */
$font-family: "Roboto", Arial, sans-serif;
$font-family-serif: Georgia, Cambria, "Times New Roman", Times, serif;
$font-family-slab: "Roboto Slab", Arial, sans-serif;
$font-family-monospace: Consolas, "Liberation Mono", Courier, monospace;

$font-weight-light: 100;
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-bold: 700;

// /* Font Size */
$font-size-x-small: 0.75rem;	// 12px 
$font-size-small: 0.8125rem;	// 13px 
$font-size-base: 0.875rem;		// 14px
$font-size: 0.875rem;			// 14px helper
$font-size-medium: 0.9375rem;	// 15px 
$font-size-large: 1.125rem;		// 18px 
$font-size-x-large: 1.5rem;		// 24px 

// /* Line Height */
$line-height-text: 1.5;
$line-height-heading: 1.5;

// /* Radius */
$border-radius-medium: 0.1875rem;
$border-radius: 0.1875rem; 		// helper
$border-radius-pill: 100rem;
$border-radius-circle: 50%;

// /* Animation */
// /* 
//  * use ease-in-out for moving point to point
//  * use ease-out for entering elements
//  * use ease-in for exiting elements
//  * use linear for opacity or color changes
//  */
$duration: 200ms;
$easing-move: ease-in-out;
$easing-enter: ease-out;
$easing-exit: ease-in;
$easing-linear: linear;

// /* Z Index */
$base-z-index: 0;

// /* Spacing */
$spacing-none: 0;				// 10px
$spacing-xxx-small: 0.125rem;	// 2px
$spacing-xx-small: 0.25rem;		// 4px
$spacing-x-small: 0.5rem;		// 8px
$spacing-small: 0.75rem;		// 12px
$spacing-medium: 1rem;			// 16px
$spacing-large: 1.5rem;			// 24px
$spacing-x-large: 2rem;			// 32px
$spacing-xx-large: 2.5rem;		// 40px

// /* Helpers */
$border: 1px solid $color-border;