/* No class declaration */
@import "./src/assets/scss/1-abstracts/abstracts";

.lib-island {
	padding: 2rem;
	background-color: #fff;
	border: 1px solid #dedede;
	border-radius: 4px;
	& + & {
		margin-top: 2rem;
	}
}

.lib-island__title {
	margin-bottom: 2rem;
}

.lib-group {
	& + & {
		margin-top: 2rem;
	}
}

.lib-group__heading {
	display: inline-block;
	font-size: 11px;
	font-weight: 800;
	color: $color-text-secondary;
	text-transform: uppercase;
	letter-spacing: 1px;
	margin-bottom: 1rem;
	border-bottom: 5px solid rgba(0,0,0,.05)
}

/* styleguide */

.lib-sidebar {
	position: relative;
	@include border-box;
	flex: 0 0 305px;
	width: 305px;
	padding: 0 30px;
	background-color: #323D47;
    overflow: hidden;
	transition: all 500ms;
}

.lib-main {
	flex: 1;
}

.lib-container {
	max-width: 710px;
	margin: 100px auto 0;
}

.lib-logo {
	@include border-box;
	height: 100px;
	// margin-bottom: 20px;
	padding: 25px 0;
	color: #fff;
	font-size: 25px;
	font-weight: 500;
}

.lib-search-box {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	padding: 1rem;
	background-color: rgba(255,255,255,.1);
	input {
		background-color: transparent;
		border: none;
	}
}

.lib-nav {
	display: flex;
}

.lib-nav__icon {
	width: 24px;
	height: 24px;
	margin-bottom: 2rem;
	fill: #BBC6D1;
	transition: all $duration $easing-linear;
	.lib-nav__parent a:hover & {
		fill: #6F9BCE;
		transition: all $duration $easing-linear;
	}
	&.is-active {
		fill: #6F9BCE;
	}
}

.lib-nav__title {
	margin-bottom: 20px;
	padding-left: 9px;
	font-size: 20px;
	font-weight: 500;
	color: #fff;
}

.lib-nav__parent {
	display: flex;
	flex-flow: column nowrap;
	padding-right: 30px;
}

.lib-nav__child {
	flex-grow: 1;
	padding-left: 20px;
	border-left: 1px solid rgba(255,255,255,.05);
}



.lib-nav-item {
	width: 100%;
	padding: 14px 0;
	border-bottom: 1px solid rgba(255,255,255,.05);
	&:last-of-type {
		border-bottom: none;
	}
}
.lib-nav-item__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
	padding: 0 9px;
	cursor: pointer;
	.icon-ui {
		fill: rgba(255,255,255,.6);
		stroke: transparent;
		transform: rotate(0deg);
		transition: transform 200ms;
	}
	&:hover .icon-ui {
		transform: rotate(-90deg);
		transition: transform 200ms;
	}
	&.is-active .icon-ui {
		transform: rotate(-180deg);
		transition: transform 200ms;
	}
}
.lib-nav-item__title {
	font-size: 15px;
	font-weight: 500;
	color: #fff;
}
.lib-nav-item__menu {
	margin-top: 16px;
	li {
		margin-bottom: 7px;
	}
	a {
		display: inline-block;
		padding: 5px 10px;
		color: #7D8996;
		font-size: 14px;
		border-radius: $border-radius;
		background-color: transparent;
		transition: all $duration $easing-linear;
		&:hover, &.is-active {
			color: #fff;
			background-color: rgba(255,255,255,.05);
			background-color: #505F69;
			transition: all $duration $easing-linear;
		}

	}
}

.lib-page-title {
	margin-bottom: 3rem;
	font-size: 22px;
	font-weight: 500;
}


///


.lib-example-item {
	padding: 2.25rem 0;
	border-bottom: $border;
}


.lib-example-item__face {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
}

.lib-example-item__content {
	display: flex;
	align-items: center;
	flex-grow: 1;
}
.lib-example-item__title {
	width: 200px;
	font-weight: normal;
}
.lib-example-item__component {
	flex-grow: 1;
}
.lib-example-item__action {
	padding-left: 6rem;
}

.lib-example-item__footer {
	padding-top: 2.25rem;
}

.lib-code {
	border: $border;
	border-radius: $border-radius;
}

.lib-code__field {
	padding: 1rem;
}
